/**
 * 刷新后保留路由params 到session中，但是params中的函数无法保存,同样传递无法JSON.stringify的对象也是无法保存的
 *  */
export default {
  beforeCreate(){
    if(!this.$route)return;
    const JSONParams = sessionStorage.getItem(this.$route.name+'__params');
    if(JSONParams){
      const params = JSON.parse(JSONParams);
      for(let key in params){
        if(params.hasOwnProperty(key)){
          this.$route.params[key] = params[key]
        }
      }
      sessionStorage.removeItem(this.$route.name+'__params')
    }
    if(Object.keys(this.$route.params).length !== 0){
      window.addEventListener('beforeunload',()=>{
        const params = {};
        for(let key in this.$route.params){
          if(this.$route.params.hasOwnProperty(key) && typeof this.$route.params[key] !== 'function'){
            params[key] = this.$route.params[key]
          }
        }
        try{
          sessionStorage.setItem(this.$route.name+'__params',JSON.stringify(params))
        }catch(e){
          console.log('params参数保存失败')
        }
      })
    }
  },
} 